

.stars__container {
  background: radial-gradient(ellipse at bottom, var(--color-black) 0%, var(--color-black) 100%);
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

$shooting-time: 10000ms;

.night {
  position: relative;
  width: 200%;
  height: 200%;
  transform: rotateZ(45deg) translateX(0) translateY(0);
  // animation: sky 200000ms linear infinite;
}

.shooting_star {
  position: absolute;
  left: 20%;
  top: 20%;
  // width: 100px;
  height: 2px;
  border-radius: 999px;
  filter: drop-shadow(0 0 6px var(--color-green));
  animation:
    tail $shooting-time ease-in-out infinite,
    shooting $shooting-time ease-in-out infinite;

   &:nth-child(n) {
    background: linear-gradient(-45deg, rgba(0, 0, 255, 0), var(--color-green), var(--color-border-green));
   }
   &:nth-child(2n+1) {
    background: linear-gradient(-45deg, rgba(0, 0, 255, 0), var(--color-red), var(--color-border-red));
   }


  // &::before {
  //   content: '';
  //   position: absolute;
  //   top: calc(50% - 1px);
  //   right: 0;
  //   // width: 30px;
  //   height: 2px;
  //   background: linear-gradient(-45deg, rgba(0, 0, 255, 0), var(--color-green), var(--color-border-green));
  //   transform: translateX(50%) rotateZ(45deg);
  //   border-radius: 100%;
  //   animation: shining $shooting-time ease-in-out infinite;
  // }

  &::after {
    // CodePen Error
    // @extend .shooting_star::before;
    display: block;
    -webkit-animation: shining 6s linear infinite;
    animation: shining 6s linear infinite;
    border-radius: 50%;
    content: "";
    height: 15px;
    width: 15px;
    position: absolute;
    right: 0;
    top: calc(50% - 7.5px);
    -webkit-transform: translateX(50%) ;
    transform: translateX(50%) ;
  }

  @for $i from 1 through 17 {
    &:nth-child(#{$i}) {
      $delay: random(9999)+0ms;
      top: calc(80% - #{random(900) - 50px});
      left: calc(20% - #{random(800) + 0px});
      animation-delay: $delay;
      // opacity: random(50) / 400 + 0.5;
      &::after {
        background-image: url('../../images/cryptoLogos/#{$i}.png');
        background-size: 15px 15px;
        background-position: center;
        background-color: rgba(0, 0, 0, 0.5);
      }
    }
  }
}

@keyframes tail {
  0% {
    width: 0;
  }

  30% {
    width: 100px;
  }

  100% {
    width: 0;
  }
}

@keyframes shining {
  0% {
    opacity: 0.6;
  }

  30% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes shooting {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(1600px);
  }
}

@keyframes sky {
  0% {
    transform: rotate(45deg);
  }

  100% {
    transform: rotate(45 + 360deg);
  }
}