
@import url('https://fonts.googleapis.com/css2?family=Acme&display=swap');
@import  './mixins.scss';


*{
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

html, body {
    overflow-x: hidden;
    position: relative;
    font-family: 'Acme', sans-serif;
    font-size: 16px;
    font-weight: 400;
    // background: url('../images/bgImages/space.jpg') center / 100% fixed;
    // background: var(--color-bg-grey);
    background: transparent;
}

body {
    padding: 5rem 0 0;
}

input, button, textarea {
    outline: none;
    &:focus {
        outline: none;
    }
    font-family: 'Acme', sans-serif;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}
.container {
    position: relative;
    display: block;
    padding: 0 1rem;
    margin: 0 auto;
}


:root {
    --color-white: #fff;
    --color-light: rgb(175, 175, 175);
    --color-black: #000;
    --color-bg-grey: rgb(34, 34, 34);
    --color-bg-black: rgba(21, 21, 21, 0.8);
    --color-border-green: rgb(51, 71, 2);
    --color-border-red: rgb(116, 21, 35);
    --color-disabled: rgba(220, 220, 220, 0.8);
    --color-green: rgb(50 200 50);
    --color-red: rgb(242 51 51);
}


.btn {
    cursor: pointer;
    font-family: 'Acme', sans-serif;
    display: inline-block;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    color: var(--color-white);
    border: 2px solid var(--color-white);
    background: transparent;
    border-radius: 0.5rem;
    transition: 0.3s linear;
    &:hover {
        color: var(--color-bg-black);
        border-color: var(--color-white);
        background: var(--color-white);
    }

    &.active {
        color: var(--color-bg-black);
        border-color: var(--color-white);
        background: var(--color-white);
    }
}



.loader {
    position: relative;
    width: 100%;
    height: calc(100vh - 80px);

    span {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 10rem;
        height: 10rem;
        font-size: 0;
        transition: 0.2s linear;
        transform: translate(-50%, -50%);
        animation: loader 2s 0s  infinite ;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background: url('../images/logo__vars/Logo_250px.png') center 90%/90% no-repeat;
        width: 100%;
        height: 100%;
        font-size: 0;
    }
}
}



@keyframes loader {
    0% {
      transform:  translate(-50%, -50%) rotate(0deg);
    }
  
    100% {
      transform:  translate(-50%, -50%) rotate(720deg);
    }
  }

// header
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    max-height: 5rem;
    z-index: 100;
    background: var(--color-bg-grey);
    box-shadow: 0px 0px 15px 10px;
    display: flex;
    align-items: center;
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.header__logo {
    user-select: none;
    display: flex;
    align-items: center;
    gap: 0.2rem;
    height:4rem;
    img {
        // width: 40px;
        // aspect-ratio: 1/1;
        height: 100%;
        width: auto;
    }
    h1 {
    letter-spacing: 2px;
    white-space: nowrap;
    @include gradientText()
    }
}

// bubbleButtons
.header__selects {
    display: flex;
    gap: 1rem;
    align-items: center;
}


.data__updater {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    animation-name: loading;
    animation-duration: 59.99s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    transform-origin: left;
    background-color: var(--color-green);
}

@keyframes loading {
    0% {
        left: 0;
        width: 0;
    }

    100%{
        width: 100%;
    }
}

.physics__buttons {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    display: grid;
    gap: 1rem;
    justify-content: space-around;

    padding: 1rem 0;
}

.physics__btn {
    max-width: 7rem;
    font-size: 1rem;
    font-weight: bold;
    padding: 0.5rem 1.2rem;
    font-family: 'Acme', sans-serif;
    display: inline-block;
    border: 2px solid var(--color-white);
    transition: 0.2s linear;
    @include glowHover($colorText: var(--color-white), $colorBg: var(--color-black), $gradient1: var(--color-white), $gradient2: var(--color-light));

    &.active {
        @include glowHover($colorText: var(--color-white), $colorBg: var(--color-black), $gradient1: var(--color-green), $gradient2: var(--color-red));
        border-color: transparent;
    }
    &:hover {
        color: var(--color-white);
    }

    &:active {
        transform: scale(0.9);
    }
} 


.my__select {
    position: relative;
    display: flex;
    align-items: center;
    width: 9rem;
    display: none;
    
    &::after, 
    &::before {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background: var(--color-white);
        height: 2px;
        width: 10px;
        transition: 0.2s linear;
    }

    &::before {
        transform: translateY(-50%) rotate(-45deg);
        right: 1.4rem;
    }

    &::after {
        transform: translateY(-50%) rotate(45deg);
        right: 1rem;
    }

    &.active {
        &::before {
            transform: translateY(-50%) rotate(-135deg);
            right: 1.4rem;
        }
    
        &::after {
            transform: translateY(-50%) rotate(135deg);
            right: 1rem;
        }
    }
}

#top__select {
    display: block;
}

.my__select-input {
    position: relative;
    width: 100%;
    border: 2px solid var(--color-white);
    border-radius: 0.5rem;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background: transparent;
    color: var(--color-white);
    text-transform: capitalize;
    text-align: left;
}

.my__select-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 85%;
    left: 0;
    width: 100%;
    background: var(--color-bg-grey);
    box-shadow: 0px 10px 10px 2px #000;
    z-index: 110;
    border: 2px solid var(--color-white);
    border-top: none;
    border-radius: 0 0 0.5rem 0.5rem;
    transition: opacity 0.2s linear;
    transform: translateY(-300%);
    opacity: 0;

    &.active {
        opacity: 1;
        transform: translateY(0);
    }
    
    li {
        padding: 0.2rem 1rem;
        text-align: left;
        color: var(--color-white);
        background: transparent;
        transition: 0.2s linear;
        user-select: none;
        cursor: pointer;
        &:hover {
            background: var(--color-light);
            color: var(--color-black);
        }

        &.active {
            background: var(--color-white);
            color: var(--color-black);
        }

        &::first-letter {
            text-transform: uppercase;
        }
    }
}

.bubbles__size {
    input{
        -webkit-appearance: none;
  margin-right: 15px;
  width: 200px;
  height: 7px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 0.5rem;
        background: linear-gradient(90deg, rgba(242,51,51,1) 0%, rgba(118,121,9,1) 51%, rgba(50,200,50,1) 100%);
        background-size: 70% 100%;
  background-repeat: no-repeat;
      
      &::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background:  var(--color-red);
        box-shadow: 0 0 2px 0 #555;
        transition: background .3s ease-in-out;
      }
      &::-webkit-slider-thumb:hover {
        background: #ff0200;
      }
    }
}



// Bubbles
.bubbles {
    position: relative;
    width: 100%;
    height:calc( 100vh - 5rem);
    margin-bottom: 2rem;
    background: var(--color-bg-black);
    canvas {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        &:first-child {
            z-index: 10;
        }
    
    }
}

.bubble {
    user-select: none;
    width: 100%;
    position: absolute;
    padding: 0.5rem;
    border-radius: 50%;
    border: 2px solid #000;
    aspect-ratio: 1/1;
    overflow: hidden;
    // transition: 1s linear;
    background-color: rgba(255, 255, 89, 0.069);
    box-shadow: 0px 0px 10px 6px rgba(187, 231, 11, 0.443) inset;
    &.bubble__increase {
        background-color: rgba(5, 222, 5, 0.15);
        box-shadow: 0px 0px 10px 6px rgba(5, 222, 5, 0.6) inset;
    }

    &.bubble__decrease {
        background-color: rgba(166, 53, 53, 0.15);
        box-shadow: 0px 0px 10px 6px rgba(166, 53, 53, 0.8) inset;
    }
} 


.bubble__items {
    position: absolute;
    width: 100%;
    height: 100vh;
}

.bubble__content {
    color: var(--color-white);
    text-transform: uppercase;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    aspect-ratio: 1/1;
}

.bubble__symbol {
    width: 100%;
    text-align: center;
    flex: 1;
    display: inline-block;

}

.bubble__change {
    width: 100%;
    text-align: center;
    flex: 1;
    display: inline-block;
}

.bubble__image {
    border-radius: 50%;
    aspect-ratio: 1/1;
    margin-bottom: 3px;
    flex: 1;
    &.max {
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       aspect-ratio: 1/1;
       display: flex;
       justify-content: center;
       align-items: center;
       img {
        width: 80%;
       }
    }
    img {
        display: block;
        border-radius: 50%;
        width: 100%;
        aspect-ratio: 1/1;
    }
}


.bubbles__items {
    position: absolute;
    transform: translate(-1000px, 1000px);
    opacity: 0;
}





// Coins

.coins__container {
    margin-bottom: 6rem;
}

.coins {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: var(--color-bg-black);
    max-width: 60rem;
    width: 100%;
    padding: 1rem 1rem;
    // border: 2px solid var(--color-border-green);
    border-radius: 1rem;
    box-shadow: 0px 0px 10px 10px var(--color-bg-black);
}


// CoinsList
.coins__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.5rem;
    width: 100%;
    margin-bottom: 2rem;
}

.coin__item {
    color: var(--color-white);
    width: 100%;
    position: relative;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    align-items: center;
    justify-content: space-between;
    padding: 0.6rem 1rem;
    height: 4rem;
    border: 3px solid;
    transition: 0.2s ease-in-out;
    border-radius: 0.5rem;
    &.increase {
        border-color: var(--color-border-green);
        @include glowHover( $gradient1: var(--color-green), $gradient2: var(--color-border-green));
    }

    &.decrease {
        border-color: var(--color-border-red);
        @include glowHover($gradient1: var(--color-red), $gradient2: var(--color-border-red));
    }

    div {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .coin__change.increase,
    .coin__volume.increase {
        color: var(--color-green);
    }
    .coin__change.decrease,
    .coin__volume.decrease {
        color: var(--color-red);
    }
}

div.coin__head {
    display: flex;
    justify-content: flex-start;
    gap: 0.5rem;
}

.coin__name {
    font-weight: bold;
    text-transform: uppercase;
}

.coin__image  {
    border-radius: 50%;
    height: 100%;
    aspect-ratio: 1/1;
    overflow: hidden;
    position: relative;
    img {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
    }
}

.coins__sorting {
    font-size: 1rem;
    color: var(--color-white);
    padding: 0.6rem 1rem;
    width: 100%;
    height: 4rem;
    border: 3px solid var(--color-border-green);
    border-radius: 1rem;
    transition: 0.2s ease-in-out;
    display: flex;
    align-items: center;
    margin-bottom: 1rem ;
    ul {
        width: 100%;
        display: grid;
        grid-template-columns: 1.5fr 1fr 1fr 1fr;
        align-items: center;

    .sort {
        user-select: none;
        cursor: pointer;
        white-space: nowrap;
        position: relative;
        span {
            display: inline-block;
            width: 6px;
            height: 10px;
            position: relative;
            margin-left: 0.4rem;
        &::after, 
        &::before {
            opacity: 0;
            content: '';
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            background: var(--color-white);
            height: 2px;
            width: 6px;
            transition: 0.2s linear;
        }
    
        &::before {
            transform: translateY(-50%)  rotate(-45deg);
            right: 0.2rem;
        }
    
        &::after {
            transform: translateY(-50%)rotate(45deg);
            right: 0%;
        }
    }
    &.active{
        text-shadow: var(--color-white) 1px 0 10px;
        span {
                &::before {
                    opacity: 1;
                    box-shadow: var(--color-white) 1px 0 10px;
                    transform: translateY(-50%) rotate(45deg);
                }
            
                &::after {
                    opacity: 1;
                    box-shadow: var(--color-white) 1px 0 10px;
                    transform: translateY(-50%) rotate(-45deg);
                }
        }
    }
    &.active-twice{
        text-shadow: var(--color-white) 1px 0 10px;
        span {
                &::before {
                    opacity: 1;
                    box-shadow: var(--color-white) 1px 0 10px;
                    transform: translateY(-50%) rotate(-45deg);
                }
            
                &::after {
                    opacity: 1;
                    box-shadow: var(--color-white) 1px 0 10px;
                    transform: translateY(-50%) rotate(45deg);
                }
        }
    }
}
    }
}
#volumeChange {
    text-align: center;
}

.coins__filter-reset  {
    color: var(--color-white);
    margin-bottom: 1rem;
    &:hover {
        color: var(--color-black);
    }

}

.coinslist__btn {
    border-radius: 0.5rem;
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
}




// List buttons
.coins__btns {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 2rem;
}

.coins__btn {
    padding: 0.5rem 1.2rem;
    border-radius: 0.5rem;
    transition: 0.3s linear;
    font-size: 1.2rem;
    cursor: pointer;
    position: relative;
    background: transparent;
    border: 2px solid var(--color-white);
    color: var(--color-white);
    overflow: hidden;
    z-index: 0;
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
       z-index: -1;

    }
    
    // &:first-child:after {
    //     background: url('../images/buttonsbg/gainersLosers.jpg')   no-repeat center;
    //     background-size: 100% ;
    //     opacity: 0.5;
    // }

     
    // &:last-child::after {
    //     background: url('../images/buttonsbg/popular.jpg')   no-repeat center;
    //     background-size: 100% ;
    //     opacity: 0.8;
    // }
}



// slick-slider
.coins {
.slick-slider {
    width: 100%;
    position: relative;
    max-width: 55rem;
    margin-bottom: 2rem;
}

.slick-arrow {
    position: absolute;
    &.slick-next {
        right: 20%;
        top: -9rem;
    }
    &.slick-prev {
        left: 20%;
        top: -9rem;
    }

    &.slick-disabled {
        box-shadow:  0px 0px 10px 2px var(--color-white);
    }
}

}



// contacts
.contacts {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 25rem;
    margin-bottom: 3rem;
}

.contacts__desc {
    color: var(--color-white);

    .contacts__title {
        font-size: 2rem;
        font-weight: 500;
        margin-bottom: 1rem;
        span {
            font-weight: bold;
            @include gradientText()
        }
    }
    .contacts__text {
        width: 100%;
        font-size: 1.2rem;
        line-height: 1.5;

        p {
            margin-bottom: 0.8rem;
        }
    }
}

.contacts__list {
    gap: 1rem;
    min-width: 15rem;
    width: min-content;
    @include motherButton(var(--color-white), var(--color-black), var(--color-white));
}



//footer
.footer {
    width: 100%;
    position: relative;
    bottom: 0;  
    left: 0;
    color: var(--color-white);
}

.footer__content {
    width: 100%;
    margin: 0 auto;
    padding: 2rem 2rem;
    display: flex;
    justify-content: space-around;
    gap: 2rem;
}

.footer__title {
    width: 100%;
    display: inline-block;
    font-size: 2rem;
    margin-bottom: 1rem;
}

.footer__subtitle {
    font-size: 1.5rem;
    margin-bottom: 0.6rem;
}

.footer__support {
    max-width: 25rem;
    width: 100%;
}

.footer__support-inner {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
}
.footer__version {
    width: 100%;
    display: flex;
    justify-content: center;
    height: 4rem;
    align-items: flex-start;
    font-size: 1.4rem;
}



.footer__links {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
}

.footer__link {
    max-width: 12rem;
    width: 100%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
        width: 100%;
        border-radius: 0.5rem;
        padding: 0.5rem 0.6rem;
        margin-bottom: 0.4rem;
        svg {
            margin-right: 0.4rem;
            width: 1.5rem;
            height: 1.5rem;
        }
}

.footer__register {
    .footer__link {
        justify-content: space-around;
        &:nth-child(1) {
            @include glowHover($colorText: #111, $colorBg: #e4b00a, $gradient1: #e4b00a, $gradient2: #1f1f0276)
        }

        &:nth-child(2) {
            @include glowHover($colorText: #111, $colorBg: #f7a600, $gradient1: #f7a600, $gradient2: #1f1f0276)
        }

        &:nth-child(3) {
            @include glowHover($colorText: #fff, $colorBg: #24ae8f, $gradient1: #24ae8f, $gradient2: #1f1f0276)
        }
    }
}


.footer__donate {
    .footer__link {
        font-size: 1rem;
        cursor: copy;
        .adress svg {
            width: 0.8rem;
            height: 0.8rem;
            margin: 0 0.2rem;
            line-height: 1.6;
        }
        &:nth-child(1) {
            @include alternateButton($color: #111, $colorBg: #e4b00a);
        }

        &:nth-child(2) {
            @include alternateButton($color: #fff, $colorBg: #262c3a );
        }

        &:nth-child(3) {
            @include alternateButton($color: #fff, $colorBg: #24ae8f);
        }

        &:nth-child(4) {
            @include alternateButton($color: #fff, $colorBg: #f0b90b);
        }
    }
}


.mobileError {
    font-size: 2rem;
    color: var(--color-white);
    text-align: center;
    padding: 5rem 0;
}


@media(max-width: 770px) {
    .physics__buttons {
       display: none;
    }

    .my__select {
        display: block;
    }

    .coins {
    .slick-arrow {
        white-space: nowrap;
        position: absolute;
        &.slick-next {
            right: 10%;
        }
        &.slick-prev {
            left: 10%;
        }
    }
}
}


@media(max-width: 570px) {
.coins__sorting {
    text-align: center;
    ul {
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    }
    .sort:last-child {
        display: none;
    }
}

.header__selects {
    flex-direction: column;
    gap: 0.5rem;
    .my__select {
        width: 8rem;
    }
    .my__select-input {
        font-size: 0.8rem;
        padding: 0.2rem 0.5rem;
    }
}

.coins__list{
    .coin__item {
        grid-template-columns: repeat(3, 1fr);
        gap: 1rem;
        .coin__volume {
            display: none;
        }
        .coin__image {
            width: 2rem;
            height: 2rem;
        }
    }
}
}


@media (max-width:500px ) {
.footer__links {
    
    .footer__subtitle {
        text-align: center;
    }

    .footer__donate {
        width: 12rem;
    }
}

.footer__content {
    flex-wrap: wrap;
}


.footer__title {
    text-align: center;
}

.footer__support-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.coins {
        .slick-arrow {
                width: min-content;
                &.slick-next {
                    left: 50%;
                    transform: translateX(-50%);
                    top: -9rem;
                }
                &.slick-prev {
                    left: 50%;
                    transform: translateX(-50%);
                    top: -13rem;
                }
            }
    }


}


@media (max-width:440px ){
    body {
        padding: 7rem 0 0 0;
    }
    .header {
        max-height: 7rem;
    }
    .header__container  {
        flex-wrap: wrap;
        justify-content: center;
        img {
            height: 3rem;
        }
    }
    
    .header__selects {
        flex-direction: row;
    }
}

@media (max-width:375px ) { 
    .header__container {
        gap: 0rem;
    }
    .header__logo {
        font-size: 1rem;
        img {
            height: 3rem;
        }
    }

    .coin__item {
        font-size: 0.9rem;
    }

    .coins__list{
        .coin__item {
            grid-template-columns: repeat(3, 1fr);
            gap: 0.5rem;
            .coin__volume {
                display: none;
            }
            .coin__image {
                width: 1.5rem;
                height: 1.5rem;
            }
        }
    }

    .coins__sorting {
        font-size: 0.8rem;

    }
}