@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}

 @mixin motherButton($color: var(--color-white), $bgColor: var(--color-black), $bgColorHover: var(--color-white))  {
        background: $bgColor;
        font-size: 1.5rem;
        border: 2px solid $color;
        color: $color;
        padding: 1rem 1.5rem;
        border-radius: 0.5rem;
        cursor: pointer;
        transition: 0.4s all ease-in-out;
        position: relative;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
      
        &:hover {
          background: $color;
          border-color: $color;
          color: $color;
        }
        
      
        &:hover .motherButton__body {
            .motherButton__item {
            transform: translateY(-30%);
            transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55); /* easeInOutBack */
      
            &:nth-child(1) {
              transition-delay: 30ms;
            }
            &:nth-child(2) {
              transition-delay: 60ms;
            }
            &:nth-child(3) {
              transition-delay: 90ms;
            }
            &:nth-child(4) {
              transition-delay: 120ms;
            }
            &:nth-child(5) {
              transition-delay: 150ms;
            }
          }
        }
        
      
        .motherButton__body {
        position: absolute;
        left: 0;
        right: 0;
        display: flex;
        bottom: 0;
        justify-content: space-around;
            .motherButton__item {
                display: flex;
                justify-content: center;
                align-items: center;
                color: $color;
                text-decoration: none;
                width: min-content;
                aspect-ratio: 1/1;
                border-radius: 0.5rem;
                background: $bgColor;
                padding: 0.5rem;
                transform: translateY(100%);
                transition: all 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55); /* easeInOutBack */
                transition: 0.2s linear;

                &:hover {
                    box-shadow:  0px 0px 10px 2px $bgColor ;
                }

                &:nth-child(1) {
                    transition-delay: 20ms;
                }
                &:nth-child(2) {
                    transition-delay: 60ms;
                }
                &:nth-child(3) {
                    transition-delay: 100ms;
                }
                &:nth-child(4) {
                    transition-delay: 80ms;
                }
                &:nth-child(5) {
                    transition-delay: 100ms;
                }
            }
    }     
    @content
 }


@mixin gradientText($color1: var(--color-green), $color2: var(--color-red)) {
    display: inline-block;
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
    background-image: linear-gradient(45deg, $color1, $color2);
    @content

}   

@mixin gradientBG($gradient1: var(--color-green), $gradient2: var(--color-red)) {
    position: relative;
    &:before {
        content: '';
        background: linear-gradient(45deg, $gradient1, $gradient2);
        position: absolute;
        top: -2px;
        left:-2px;
        background-size: 400%;
        z-index: -1;
        filter: blur(5px);
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        animation: glowing 20s linear infinite;
        opacity: 0;
        transition: opacity .3s ease-in-out;
        border-radius: 10px;
    }
    &:active:after {
        background: transparent;
    }
    
    &:hover:before {
        opacity: 1;
    }
    
}


@mixin glowHover($colorText: var(--color-white), $colorBg: var(--color-black), $gradient1: var(--color-green), $gradient2: var(--color-red)) {
    outline: none;
    color: $colorText;
    background: $colorBg;
    cursor: pointer;
    position: relative;
    z-index: 1;
    border-radius: 0.5rem;

&:before {
    content: '';
    background: linear-gradient(45deg, $gradient1, $gradient2,  $gradient1, $gradient2, $gradient1, $gradient2, $gradient1, $gradient2, $gradient1, $gradient2);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(6px);
    width: calc(100% + 5px);
    height: calc(100% + 5px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 0.5rem;
}

&:active {
    color: $colorText;
}

&:hover:before {
    opacity: 1;
}

&.active:before {
    opacity: 1;
}

&:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: $colorBg;
    left: 0;
    top: 0;
    border-radius: 0.5rem;
}
@content;
}



@mixin alternateButton($color, $colorBg) {
  position: relative;
  overflow: hidden;
  border: none;
  color: $color;
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
  background: $colorBg;
  touch-action: manipulation;


    span {
    
    &:first-child {
        position: relative;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: color 600ms cubic-bezier(0.48, 0, 0.12, 1);
        z-index: 10;
    }
    &:last-child {
        color: $color;
        position: absolute;
        bottom: 0;
        transition: all 500ms cubic-bezier(0.48, 0, 0.12, 1);
        z-index: 100;
        opacity: 0;
        top: 50%;
        left: 50%;
        width: 70%;
        padding: 0.7rem 0.6rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transform: translateY(100%) translateX(-50%);
        height: 100%;
    }
    }

    &::after {
    content: "";
    position: absolute;
    bottom: -50%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $colorBg;
    transform-origin: bottom center;
    transition: transform 600ms cubic-bezier(0.48, 0, 0.12, 1);
    transform: skewY(9.3deg) scaleY(0);
    z-index: 50;
    }

    &:hover:after {
        transform-origin: bottom center;
        transform: skewY(9.3deg) scaleY(2);
    }

    &:hover span:last-child {
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        opacity: 1;
        transition: all 900ms cubic-bezier(0.48, 0, 0.12, 1);
    }

    @content
}